.main{
    width: 100%;
    height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.loader {
    border: 10px solid #f3f3f3;
    border-top: 10px solid green;
    border-bottom: 10px solid green;
    border-radius: 50%;
    width: 80px;
    height: 80px;
    animation: spin 0.2s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }